import { Injectable } from '@angular/core';
import { CurrentWeather } from './current-weather';
import { Http, Response } from '@angular/http';
import { map } from 'rxjs/operators';
import { Forecast } from './forecast';

@Injectable({
  providedIn: 'root'
})
export class WeatherService {
  myWeather: CurrentWeather;
  location: any;
  constructor(private http: Http) {}

  localWeather() {
    return new Promise((res, rej) => {
      navigator.geolocation.getCurrentPosition(pos => {
        this.location = pos.coords;
        const lat = this.location.latitude;
        const lon = this.location.longitude;
        return this.http
          .get(
            `https://api.openweathermap.org/data/2.5/weather?lat=${lat}&lon=${lon}&appid=94269e1a4906f79f68bbedda61f249e4&units=metric`
          )
          .pipe(map((response: Response) => response.json()))
          .toPromise()
          .then(data => {
            console.log(data);
            this.myWeather = new CurrentWeather(
              data.name,
              data.main.temp,
              data.weather[0].icon,
              data.weather[0].description,
              data.main.humidity,
              data.main.temp_max,
              data.main.temp_min
            );
            res(this.myWeather);
          });
      });
    });
  }

  anotherCityWeather(city: string) {
    return this.http
      .get(
        `https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=94269e1a4906f79f68bbedda61f249e4&units=metric`
      )
      .pipe(map((response: Response) => response.json()));
  }

  fiveDayForecast(city: string) {
    return this.http
      .get(
        `https://api.openweathermap.org/data/2.5/forecast?q=${city}&appid=94269e1a4906f79f68bbedda61f249e4&units=metric`
      )
      .pipe(map((response: Response) => response.json()));
  }
}
