import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { WeatherService } from '../weather.service';
import { CurrentWeather } from '../current-weather';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'wa-current',
  templateUrl: './current.component.html',
  styleUrls: ['./current.component.css']
})
export class CurrentComponent implements OnInit {
  location: Coordinates;
  constructor(private ws: WeatherService, private route: ActivatedRoute) {}
  myWeather: CurrentWeather;
  ngOnInit() {
    this.route.data.subscribe((data: { myWeather: CurrentWeather }) => {
      this.myWeather = data.myWeather;
    });
  }

  onSubmit(weatherForm: NgForm) {
    this.ws.anotherCityWeather(weatherForm.value.city).subscribe(data => {
      this.myWeather = new CurrentWeather(
        data.name,
        data.main.temp,
        data.weather[0].icon,
        data.weather[0].description,
        data.main.humidity,
        data.main.temp_max,
        data.main.temp_min
      );
    });
  }
}
